}

button {
   margin: 1em;
   background-color: #323c96;
   border: 2px solid rgb(223, 223, 223);
   color: rgb(245, 245, 245);
   padding: 1em 1.5em;
   text-align: center;
   text-decoration: none;
   font-size: 1em;
   display: inline-block;
   width: 10%; /* to test the test-align property */
   border-radius: 16px;
 }

 button:hover {
   background-color: #414ec0;
 }